import * as React from "react";
import { Component } from "react";
import ReactMapGL, { FlyToInterpolator } from "react-map-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import Markers from "./maker";
import Overlay from "./overlay";
import HikeInfo from "./hikeInfo";
import HikeIcon from "../hiking_icon.ico";
import LoadingPopup from "./loadingPopup";

import mapboxgl from "mapbox-gl"; // This is a dependency of react-map-gl even if you didn't explicitly install it

// eslint-disable-next-line import/no-webpack-loader-syntax
mapboxgl.workerClass = require("worker-loader!mapbox-gl/dist/mapbox-gl-csp-worker").default;

export default class Map extends Component {
  state = {
    hikes: null,
    loading: false,
    chosenFeature: null,
    viewport: {
      latitude: 44.323,
      longitude: -99.264,
      zoom: 3.47,
    },
    mapControl: {
      dragPan: true,
      dragRotate: true,
      doubleClickZoom: true,
      scrollZoom: true,
      touchZoom: true,
      touchRotate: true,
    },
  };

  async componentDidMount() {
    this.setState({ loading: true });
    document.title = "Roses Day Hikes";
    const submit = await fetch(
      "https://guarded-wildwood-70330.herokuapp.com/getall",
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    const jsonResult = await submit.json();
    //console.log(jsonResult);
    this.setState({ hikes: jsonResult });
    this.setState({ loading: false });
  }

  _onViewportChange = (viewport) => this.setState({ viewport });

  _onClickMarker = (hike) => {
    this.setState({ chosenFeature: hike });
    this._toggleInteractive(false);
  };

  _onCompleted = async (hike, image) => {
    var imageURL = null;
    console.log(image);
    // upload image to cloudinary
    const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "dayhikes");
    data.append("cloud_name", "adamlairdcloud");
    const submitNewImage = await fetch(
      "https://api.cloudinary.com/v1_1/adamlairdcloud/image/upload",
      {
        method: "post",
        body: data,
      }
    );
    const newImageResult = await submitNewImage.json();
    console.log("hike");
    console.log(hike);
    imageURL = newImageResult.url;
    console.log(imageURL);

    // save to DB NOT WORKING!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! ?need to stringify the body = not working
    const submitNewChange = await fetch(
      "https://guarded-wildwood-70330.herokuapp.com/update",
      {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name: hike.name,
          toUpdate: [
            {
              done: true,
              pic: imageURL,
            },
          ],
        }),
      }
    );
    const newChangeResult = await submitNewChange.json();
    console.log("new change result");
    console.log(newChangeResult);

    // get update page
    // maybe ablt to just change the state here with new hike details rather than another API call

    const submit = await fetch(
      "https://guarded-wildwood-70330.herokuapp.com/getall",
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    const jsonResult = await submit.json();
    console.log("jsonResult");
    console.log(jsonResult);
    this.setState({ hikes: jsonResult });
  };

  _renderOverlay() {
    return <Overlay hikes={this.state.hikes} onClick={this._onClickMarker} />; //<h4>{hikes}</h4>;
  }

  _toggleInteractive = (bool) => {
    this.setState({
      mapControl: {
        dragPan: bool,
        dragRotate: bool,
        doubleClickZoom: bool,
        scrollZoom: bool,
        touchZoom: bool,
        touchRotate: bool,
      },
    });
  };

  _closePopup = () => {
    this.setState({
      chosenFeature: null,
    });
    this._toggleInteractive(true);
  };

  _flyToHike = (long, lat) => {
    console.log("LONG AND LAT");
    console.log(long + " " + lat);
    this.setState({
      viewport: {
        latitude: Number(lat),
        longitude: Number(long),
        zoom: 11,
        transitionInterpolator: new FlyToInterpolator({ speed: 1.2 }),
        transitionDuration: "6000",
      },
    });
  };

  _renderPopup() {
    const { chosenFeature } = this.state;

    return (
      chosenFeature && (
        <HikeInfo
          info={chosenFeature}
          func={this._onCompleted}
          closeFunc={this._closePopup}
          flyFunc={this._flyToHike}
        />
      )
    );
  }

  render() {
    const { viewport, hikes, mapControl } = this.state;
    return (
      <ReactMapGL
        {...viewport}
        {...mapControl}
        width="100vw"
        height="100vh"
        mapStyle="mapbox://styles/mapbox/outdoors-v11"
        minZoom={2.47}
        onViewportChange={this._onViewportChange}
        mapboxApiAccessToken="pk.eyJ1IjoibGFpcmR5NTQ1MiIsImEiOiJja2d5OHkzeDMwcmJjMndwaWJjdG44ZXY4In0.Y0c3nuXuC0tNYpf5QX3wSA"
      >
        {this.state.loading ? (
          <LoadingPopup />
        ) : (
          <Markers data={hikes} onClick={this._onClickMarker} />
        )}
        {this._renderPopup()}
        {this._renderOverlay()}
      </ReactMapGL>
    );
  }
}

