import React, { useState } from "react";
import styled from "@emotion/styled/macro";

const ImageContainer = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
`;

const Image = styled.img`
  width: 24vw;
  height: 80vh;
  margin-right: 2px;
  margin-left: 2px;
  object-fit: fill;
`;

const Comparison = ({ pos, pro }) => {
  const myPhotoFolder = "./Images/Me/";
  const proPhotoFolder = "./Images/" + pro;

  const myPhotoDTL = myPhotoFolder + "7i_DTL_p" + pos.toString();
  const myPhotoFO = myPhotoFolder + "7i_FO_p" + pos.toString();
  const proPhotoDTL = proPhotoFolder + "/DTL_p" + pos.toString();
  const proPhotoFO = proPhotoFolder + "/FO_p" + pos.toString();

  console.log(proPhotoDTL + ".jpg");

  return (
    <ImageContainer>
      <Image src={myPhotoDTL + ".jpg"} />
      <Image src={proPhotoDTL + ".png"} />
      <Image src={myPhotoFO + ".jpg"} />
      <Image src={proPhotoFO + ".png"} />
    </ImageContainer>
  );
};

export default Comparison;
