import React from "react";
import { PureComponent } from "react";
import styled from "@emotion/styled/macro";
import { COLORS } from "./Theme/theme";

/*
const CompleteHolder = styled.div`
  position: absolute;
  bottom: 0px;
  width: 100vw;
  align-content: center;
  align-items: center;
`;
*/

const BlockHolder = styled.div`
  position: absolute;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  top: 10px;
  left: 5vw;
  height: 5vh;
  width: 90vw;
`;

const Block = styled.div`
  height: 5vh;
  width: 5vh;
  cursor: pointer;
`;

const BlockCircle = styled.div`
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  text-align: center;
  line-height: 2.5rem;
  font-size: 1.2rem;
  &:hover {
    border: solid ${COLORS.Black} 3px;
    font-size: 1.5rem;
    line-height: 2.2rem;
  }
`;

// add hover and go to ?

export default class Overlay extends PureComponent {
  render() {
    const { hikes, onClick } = this.props;

    if (!hikes) return null;

    var total = hikes.length;
    var complete = 0;
    for (let i = 0; i < hikes.length; i++) {
      if (hikes[i].done) complete++;
    }

    var completePercent = (complete / total) * 100;
    console.log(completePercent); // can use for some form of complete bar

    const items = hikes.slice(0, 25).map((hikes, index) => (
      <Block key={hikes.name} onClick={() => onClick(hikes)}>
        <BlockCircle
          style={
            hikes.done
              ? {
                  backgroundColor: `${COLORS.Green}`,
                }
              : { backgroundColor: `${COLORS.Red}` }
          }
        >
          {index + 1}
        </BlockCircle>
      </Block>
    ));

    const items2 = hikes.slice(25, 50).map((hikes, index) => (
      <Block key={hikes.name} onClick={() => onClick(hikes)}>
        <BlockCircle
          style={
            hikes.done
              ? {
                  backgroundColor: `${COLORS.Green}`,
                }
              : { backgroundColor: `${COLORS.Red}` }
          }
        >
          {index + 26}
        </BlockCircle>
      </Block>
    ));

    return (
      hikes && (
        <div>
          <BlockHolder>{items}</BlockHolder>
          <BlockHolder style={{ top: "8vh" }}>{items2}</BlockHolder>
        </div>
      )
    );
  }
}

