import React, { useState } from "react";
import styled from "@emotion/styled/macro";

import { CSSTransition } from "react-transition-group";
import "../Accessories/transition.css";

import WeathercardContainer from "./weatherContainer";

const TextArea = styled.div`
  position: relative;
  color: white;
  cursor: pointer;
  text-align: center;
`;

const TopLine = styled.div`
  font-size: 1.5rem;
`;

const BottomLine = styled.div`
  font-size: 0.9rem;
  align-self: bottom;
  margin-top: 5px;
`;

const RefreshImage = styled.img`
  position: absolute;
  width: 20px;
  height: 20px;
  left: -5px;
  bottom: 0px;
  cursor: pointer;
`;

const TopBarWeatherText = ({ weather, loading, SearchCity, Refresh }) => {
  const [enabled, setEnabled] = useState(false);
  var temp = weather.temp;
  var condition = weather.condition;
  var today = new Date();
  var hours = today.getHours();
  var minutes = (today.getMinutes() < 10 ? "0" : "") + today.getMinutes();
  var time = hours + ":" + minutes;
  var refreshIcon = `./img/refreshIcon.png`;

  const handleClick = () => {
    setEnabled(!enabled);
    console.log(enabled);
  };

  return (
    <>
      <TextArea>
        <TopLine onClick={() => handleClick()}>
          {temp} °C, {condition}
        </TopLine>
        <BottomLine onClick={() => handleClick()}>
          {weather.city} at {time}{" "}
        </BottomLine>
        <RefreshImage src={refreshIcon} onClick={Refresh}></RefreshImage>
      </TextArea>
      <CSSTransition in={enabled} timeout={10} classNames="grow" unmountOnExit>
        <WeathercardContainer
          weather={weather}
          SearchCity={SearchCity}
          loading={loading}
        />
      </CSSTransition>
    </>
  );
};

export default TopBarWeatherText;

