import React from "react";
import styled from "@emotion/styled";

const Icon = ({ condition }) => {
  var icon = "";
  switch (condition) {
    case "Clouds":
      icon = `./img/Clouds.png`;
      break;
    case "Clear":
      icon = `./img/Clear.png`;
      break;
    case "Haze":
      icon = `./img/Haze.png`;
      break;
    case "Hail":
      icon = `./img/Hail.png`;
      break;
    case "Fog":
      icon = `./img/Fog.png`;
      break;
    case "Tornado":
      icon = `./img/Tornado.png`;
      break;
    case "Dust":
      icon = `./img/Dust.png`;
      break;
    case "Mist":
      icon = `./img/Fog.png`;
      break;
    case "Snow":
      icon = `./img/Snow.png`;
      break;
    case "Rain":
      icon = `./img/Rain.png`;
      break;
    case "Drizzle":
      icon = `./img/Drizzle.png`;
      break;
    case "Thunderstorm":
      icon = `./img/SevereThunderstorm.png`;
      break;
    default:
      icon = `./img/Fog.png`;
      break;
  }

  const Icon = styled.img`
    max-width: 128px;
    height: 64px;
    object-fit: cover;
  `;

  return <Icon src={icon} alt="Weather Icon" />;
};

export default Icon;
