import * as React from "react";
import { PureComponent } from "react";
import styled from "@emotion/styled/macro";
import FlyIcon from "./flyIcon";
import CloseIcon from "./closeIcon";
import { COLORS } from "./Theme/theme";

const DoneContainer = styled.div`
  position: fixed;
  cursor: default;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -45%);
  background-color: ${COLORS.White};
  border-radius: 10px;
  height: 75vh;
  width: 45vw;
  padding: 5px;
  border: solid 4px ${COLORS.Black};
`;

const Container = styled.div`
  position: fixed;
  cursor: default;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: ${COLORS.White};
  border-radius: 10px;
  height: 25vh;
  width: 30vw;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-style: solid;
  border-color: ${COLORS.Black};
  border-width: 4px;
`;

const HikePic = styled.img`
  position: relative;
  width: 98%;
  height: 83%;
  object-fit: contain;
  border-radius: 10px;
  margin-bottom: 0px;
`;

const CloseButton = styled.div`
  position: fixed;
  cursor: pointer;
  top: 0px;
  right: 0px;
  height: 2rem;
  width: 2rem;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  transition: transform 0.2s;
  &:hover {
    transform: scale(1.25);
  }
`;

const FlyToButton = styled.div`
  position: fixed;
  cursor: pointer;
  top: 0px;
  left: 0px;
  height: 2rem;
  width: 2rem;
  margin-top: 0.5rem;
  margin-left: 0.5rem;
  transition: transform 0.2s;
  &:hover {
    transform: scale(1.25);
  }
`;

const HikedButton = styled.div`
  width: 60%;
  height: 30%;
  border-radius: 10px;
  background-color: ${COLORS.Blue};
  font-size: 1.2rem;
  line-height: 3.5rem;
  cursor: pointer;
`;

const UploadLabel = styled.div`
  width: 60%;
  height: 30%;
  border-radius: 10px;
  background-color: ${COLORS.Blue};
  text-align: center;
  vertical-align: center;
  cursor: pointer;
`;

const UploadLabelText = styled.label`
  display: block;
  text-align: center;
  line-height: 3.5rem;
  font-size: 1.2rem;
  width: 100%;
  height: 100%;
  cursor: pointer;
`;

const NameText = styled.h2`
  font-size: 1.8rem;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
`;

export default class HikeInfo extends PureComponent {
  constructor() {
    super();
    this.state = {
      image: null,
      flying: false,
    };
  }

  _flyingFunction = (long, lat, func) => {
    console.log(this.state.flying);
    this.setState({ flying: true });
    //flyFunc(info.long, info.lat);
    func(long, lat);
    console.log(this.state.flying);

    setTimeout(() => {
      this.setState({ flying: false });
    }, 7000);
    //console.log(this.state.flying);
  };

  componentDidUpdate(prevProps) {
    if (this.props.info.name !== prevProps.info.name) {
      this.setState({ image: null });
    }
  }

  render() {
    const { info, func, closeFunc, flyFunc } = this.props;
    console.log("info");
    console.log(info);

    if (info.done) {
      return (
        <DoneContainer
          style={
            this.state.flying ? { animation: "fadeInOut 7s linear 0s 1" } : null
          }
        >
          <FlyToButton
            onClick={() => this._flyingFunction(info.long, info.lat, flyFunc)}
          >
            <FlyIcon />
          </FlyToButton>
          <CloseButton onClick={() => closeFunc()}>
            <CloseIcon />
          </CloseButton>
          <NameText>{info.name}</NameText>
          <HikePic src={info.pic} />
        </DoneContainer>
      );
    } else {
      return (
        <Container
          style={
            this.state.flying ? { animation: "fadeInOut 7s linear 0s 1" } : null
          }
        >
          <FlyToButton
            onClick={() => this._flyingFunction(info.long, info.lat, flyFunc)}
          >
            <FlyIcon />
          </FlyToButton>
          <CloseButton onClick={() => closeFunc()}>
            <CloseIcon />
          </CloseButton>
          <NameText>{info.name}</NameText>

          {this.state.image ? (
            <HikedButton
              className="hvr-outline-out"
              onClick={() => func(info, this.state.image)}
            >
              Hike completed!
            </HikedButton>
          ) : (
            <UploadLabel>
              <input
                type="file"
                onChange={(e) => this.setState({ image: e.target.files[0] })}
                id="ImageToUpload"
                style={{ display: "none" }}
              />
              <UploadLabelText
                htmlFor="ImageToUpload"
                className="hvr-outline-out"
              >
                Upload a photo
              </UploadLabelText>
            </UploadLabel>
          )}
        </Container>
      );
    }
  }
}

