import React from "react";
import "./App.css";
import MainContainer from "./Components/MainContainer";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import RDHApp from "./RosesDayHikes/App";
import GolfPool from "./GolfPool/App";
import GolfAnalysis from "./GolfAnalysis/App";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/adams-swing">
          <div className="AppRDH">
            <GolfAnalysis />
          </div>
        </Route>
        <Route path="/RosesDayHikes">
          <div className="AppRDH">
            <RDHApp />
          </div>
        </Route>
        <Route exact path="/golfpool">
          <GolfPool />
        </Route>
        <Route exact path="/">
          <div className="App">
            <MainContainer defaultLocation="Boston, US" />
          </div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
